@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400&display=swap);
* {
    box-sizing: border-box;
}

body {
    background-color: black;
    color: #FFF;
    margin: 0;
    font-family: 'Roboto', 'sans-serif';
}

.movieRow {
    margin-bottom: 0.5rem;
}

.movieRow h2 {
    margin: 0 0 0 2rem;
}

.movieRow-list{
    transition: all ease 0.4s;
}

.movieRow-listarea{
    overflow-x: hidden;
    padding-left: 2rem;
}

.movieRow-item{
    display: inline-block;
    width: 150px;
    cursor: pointer;

}

.movieRow-item img{
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.15s;
}

.movieRow-item img:hover{
    transform: scale(1);
}

.movieRow-left,
.movieRow-right{
    position: absolute;
    width: 2.7rem;
    height: 15rem;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}

.movieRow-left{
    left: 0;
}
.movieRow-right{
    right: 0;
}

.movieRow:hover .movieRow-left,
.movieRow:hover .movieRow-right{
    opacity: 1;
}

@media (max-width: 760px) {
    .movieRow-left,
    .movieRow-right{
        opacity: 1;
    }
    
}
*{
    box-sizing: border-box;
}

body{
    background-color: #111;
    color: #fff;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

.lists {
    margin-top: -10rem;
}

footer {
    margin: 3.5rem;
    text-align: center;
}

.loading{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 99;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}
.featured{
    height: 100vh;
}

.featured-vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);

}

.featured-horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 2rem;
    padding-bottom: 10rem;
    padding-top: 4.2rem;
}

.featured-name{
    font-size: 4rem;
    font-weight: bold;
}

.featured-points,
.feature-year,
.featured-seasons{
    display: inline-block;
    margin-right: 1rem;
}
.featured-seasons{
    margin-left: 1rem;
}

.featured-info{
    display: flex;
    font-style: 1.2rem;
    font-weight: bold;
}

.featured-points{
    color: #46d369;
}

.featured-description{
    margin-top: 1rem;
    font-size: 1.3rem;
    color: #999;
    max-width: 40%;
}

.featured-buttons{
    margin-top: 1rem;
}

.featured-watchButton,
.featured-myListButton{
    display: inline-block;
    font-size: 1.3rem;
    font-weight: bold;
    padding: 1rem 1.8rem;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 0.6rem;
    opacity: 1;
    transition: all ease 0.2s;
}

.featured-watchButton:hover,
.featured-myListButton:hover{
    opacity: 0.7;
}

.featured-watchButton{
    background-color: #fff;
    color: #000;
}

.featured-myListButton{
    background-color: #333;
    color: #fff;
}

.featured-genres{
    margin-top: 1rem;
    font-size: 1.3rem;
    color: #999;
}

@media (max-width: 760px) {
    .featured{
        height: 80vh;
    }
    .featured-name{
        font-size: 2.5rem;
    }
    .featured-info{
        font-size: 1rem;
    }
    .featured-description{
        font-size: 0.9rem;
        max-width: 100%;
        margin-right: 2rem;
    }
    .featured-watchButton,
    .featured-myListButton{
        font-size: 1rem;
    }
    .featured-genres{
        font-size: 0.9rem;
    }
}
header{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 4.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem;
    background: transparent;
    transition: all ease 0.6s;
}

header.black{
    background-color: #141414;
}
.header-logo{
    height: 2rem;
}

.header-logo img{
    height: 100%;
}
.header-user {
    height: 3rem;
}

.header-user img{
    height: 100%;
    border-radius: 3px;
}
